.full-width {
  padding: 80px 0;
  background-size: cover;
  background-position: center center;
  color: white;
  position: relative;

  //Overlay to help with text readability. Color and its opacity will vary
  //depending on the site's styles
  &:after {
    display: block;
    position: absolute;
    top: 0;
    content: '';
    z-index: 1;
    height: 100%;
    width: 100%;
    background-color: rgba(#000, 0.30);
  }


  &.center-align {
    text-align: center;
  }

  &.left-align {
    text-align: left;
  }

  &.right-align {
    text-align: right;
  }


  .container {
    max-width: 750px;
    //pop over dat overlay
    z-index: 2;
    position: relative;
  }

  .btn {
    margin: 30px auto 0;
  }
}
