.image-with-text {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;

  &.one-half {
    .image-with-text-image {
      flex-basis: 50%;
    }

    .image-with-text-content {
        flex-basis: 50%;
    }
  }

  &.one-third {
    .image-with-text-image {
      flex-basis: 33.3333%;
    }

    .image-with-text-content {
        flex-basis: 66.6666%;
    }
  }

  &.one-fourth {
    .image-with-text-image {
      flex-basis: 25%;
    }

    .image-with-text-content {
        flex-basis: 75%;
    }
  }

  &.left-align {
    .image-with-text-image {
      order: 1;
    }

    .image-with-text-content {
      order: 2;
      margin-left: 4%;
    }
  }

  &.right-align {
    .image-with-text-image {
      order: 2;
    }

    .image-with-text-content {
      order: 1;
      margin-right: 4%;
    }
  }
}
