.callout-repeater {
  .container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;

    .callout {
      padding: 0 2%;
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
      }

      img {
        margin-bottom: 20px;
      }

      p {
        margin-bottom: 20px;
      }

      a {
        display: block;
      }
    }
  }
}
